.header__section {
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
    
}   

.header__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: calc(100% - 40px);
    padding: 10px 20px 0 20px;
    
}

.header__logo-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.header__logo-pic-container {
    background-image: url(../../images/header/sun.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100px;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 20px;
    transition: transform 0.9s ease;
}
.header__logo-pic-container:hover {
    transform: rotate(360deg);
} 

.header__logo-pic {
    background-image: url(../../images/header/logo.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    border-radius: 50%;
    width: 60px;
    height: 60px;
    border: 2px solid rgb(236, 203, 55);
}

.header__logo-text {
    font-size: 22px;
    color: rgb(3, 28, 61);
}

.header__nav-container {
    display: flex;
    padding: 0;
}

.header__link {
    background-image: url(../../images/header/cloud1.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-size: contain;
    width: 170px;
    height: 70px;
    list-style-type: none;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    text-decoration: none;

    opacity: 0.8;
}    

.header__link:hover {
    transform: scale(1.05);
    opacity: 1;
}

.header__link-title {
    font-family: "Karton", sans-serif;
    font-weight: 600;
    color: rgb(3, 28, 61);
    font-size: 20px;
}

.header__grass {
    background-image: url(../../images/header/grass.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    width: 100%;
    max-height: 100px;
    min-height: 50px;
}

.header__grass-container {
    display: flex;
}

@media screen and (max-width: 1400px) {
    .header__logo-text {
        font-size: 20px;
        max-width: 400px;
    }
} 

@media screen and (max-width: 1300px) {
    .header__logo-text {
        font-size: 22px;
        max-width: 100%;
    }

    .header__logo-pic {
        width: 50px;
        height: 50px;
    }
    .header__wrapper {
        flex-direction: column;
        align-items: unset;
    }
    .header__nav-container {
        justify-content: flex-end;
    }
    
    .header__logo-container {
        align-self: flex-start;
    }

}

@media screen and (max-width: 830px) {
    .header__grass:nth-of-type(2){
        display: none;
    }
    .header__logo-text {
        font-size: 20px;
        max-width: 500px;
    }
}    

@media screen and (max-width: 680px) {
    .header__logo-text {
        font-size: 20px;
        max-width: 400px;
    }
}   

@media screen and (max-width: 650px) {
    .header__nav-container {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-auto-rows: auto;
        gap: 10px;
        max-width: 100%;
        margin: auto;
        margin-top: 20px;
    }
}   

@media screen and (max-width: 570px) {
    .header__logo-text {
        font-size: 18px;
        max-width: 500px;
    }

    .header__logo-pic-container {
        background-image: unset;
        width: unset;
        height: unset;
        margin-right: 0;
    }

    .header__logo-pic {
        width: 75px;
        height: 75px;
        border: 5px solid rgb(236, 203, 55);
        margin-right: 10px;
    }
}

@media screen and (max-width: 500px) {
    .header__logo-pic {
        width: 60px;
        height: 60px;
    }
}

@media screen and (max-width: 480px) {
    .header__section {
        margin-bottom: 10px;
    }  

    .header__wrapper {
        padding: 10px;
        width: calc(100% - 20px);
    }

    .header__grass {
       width: 100%;
    }

    .header__grass:last-of-type {
        display: none;
    }
}  

@media screen and (max-width: 430px) {
    .header__logo-pic {
        width: 60px;
        height: 60px;
        border: 2px solid rgb(236, 203, 55);
    }
    .header__logo-text {
        font-size: 16px;
        width: 200px;
        height: fit-content;
    }
} 

@media screen and (max-width: 400px) {
    .header__link-title {
        font-size: 16px;
    }

    .header__link {
        width: 140px;
        height: 60px;
    } 
} 